	import {PaperMedal} from 'components/ks-components/paper-medal/paper-medal'
 import {GetPerformance} from 'api/exam'
export default {
	components:{
		PaperMedal	
	},
  /**做题结果页 */
  data() {
    return {
      loaded: false,
      /**正确率 */
      accuracy: 0,
      /**批卷状态 0未批阅 1已批阅 */
      iscorrect: 0,
      /**题数集合 */
      answerSheet: [],
      /**做题返回的id */
      id: 0,
      /**做题类型 1整卷 2随机 */
      type: 0,
      /**试卷总分数 */
      correctscore: 0,
      /**正确总分数 */
      sumscore: 0,
      /**捆绑课程列表 */
      course_list:[],
	  query:{},
	  isLogin:0
    };
  },
  onLoad(query) {
	  console.log(query)
  	this.query = query
	this.id = this.query.id;
	this.type = this.query.type;
	this.$store.ready(state=>{
		this.isLogin =state.isLogin
	})
  },
  onShow() {
    
    this.answerSheet = [];
    
    var answerList = [];
    GetPerformance({
      answerid: this.id,
      type: this.type,
      paperid:this.query.paperid,
      categoryid:this.query.categoryid
    }).then((res)=> {
      if (res.result) {
      
        this.course_list = res.data.course_list
        this.iscorrect = 1;
        if(this.type==2){
          this.accuracy = res.data.zhunquelv;
        }else{
          this.accuracy = res.data.accuracy;
        }
        
        var tmarr = [];
        res.data.answer.map((item, i)=> {
          if (item.tklist) {
            item.tklist.map((tm)=> {
              // if (res.iscorrect == 0) {
              //   if (tm.userscore > 0) {
              //     tm.right = 1;
              //   } else {
              //     tm.right = 0;
              //   }
              // }
              if (tm.userscore > 0) {
                tm.right = 1;
              } else {
                tm.right = 0;
              }
              if (tm.useranswer && tm.useranswer.toString() !== "") {
                tm.doing = 1;
              } else {
                tm.doing = 0;
              }
              tmarr.push(tm);
            });
          }
        });
        this.answerSheet = tmarr;
        this.sumscore = res.data.sumscore;
      }else{
        this.iscorrect = 0;
      }
      this.loaded = true;
    });
  },
  methods: {
    openExam(){
		uni.navigateTo({
			url:`/pages/exam/index?categoryid=${this.query.categoryid}`
		})
    },
    openCourse(item){
      uni.navigateTo({
        url:'/pages/web/web?title='+item.name+'&url='+encodeURIComponent(item.view_url)
      })
    },

    htmlfilter(val){
      if(val){
          //let img = require('@/assets/common/icons.png')
          val = val.replace(/\<img[^>]+>/g,'')
          val = val.replace(/(\<[^>]+>)|&nbsp;/gi,'').trim()
          //val = val.replace(/{{img}}/g,  `<img src="${img}" />`)
          return val
      }else{
          return ''
      }
    },
    /**
     * 查看解析
     */
    viewAnalysis() {
		uni.navigateTo({
			url:`/pages/exam/analysis?name=analysis&categoryid=${this.query.categoryid}&id=${this.id}&type=${this.type}&paperid=${this.query.paperid}&title=${this.query.title}`
		})
     
    },
    /**
     * 继续练习
     */
    practice() {
      thisr.push({
        name:'exam'
      });
      
    }
  }
};
